import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.scss']
})
export class ServicesComponent implements OnInit {

  constructor(private translateService: TranslateService) { }

  services: Array<{title: string, icon: string, description: string}> = [];


  ngOnInit(): void {
    this.services = [
      {
        title: this.translateService.instant('services.webApp'),
        icon: 'icon icon-embed2',
        description: this.translateService.instant('services.webAppDesc')
      },
      {
        title: this.translateService.instant('services.mobApp'),
        icon: 'icon icon-phone_android',
        description: this.translateService.instant('services.mobAppDesc')
      },
      {
        title: this.translateService.instant('services.uiux'),
        icon: 'icon icon-edit',
        description: this.translateService.instant('services.uiuxDesc')
      },
      {
        title: this.translateService.instant('services.pwa'),
        icon: 'icon icon-stack',
        description: this.translateService.instant('services.pwaDesc')
      },
      {
        title: this.translateService.instant('services.techSupport'),
        icon: 'icon icon-miscellaneous_services',
        description: this.translateService.instant('services.techSupportDesc')
      },
      {
        title: this.translateService.instant('services.consult'),
        icon: 'icon icon-work_outline',
        description: this.translateService.instant('services.consultDesc')
      },
      {
        title: this.translateService.instant('services.training'),
        icon: 'icon icon-group',
        description: this.translateService.instant('services.trainingDesc')
      }
    ]
  }

}
