import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {


  /**
   * To hold placeholders values
   */
  namePlaceHolder: string = '';
  companyPlaceHolder: string = '';
  emailPlaceHolder: string = '';
  mobilePlaceHolder: string = '';
  messagePlaceHolder: string = '';

  /**
   * To hold values
   */
   name: string = '';
  company: string = '';
  email: string = '';
  mobile: string = '';
  message: string = '';


  constructor(private translateService: TranslateService) {
  }


  ngOnInit(): void {
    this.setPlaceHolderValues();
    
  }

  /**
   * To set placeholder values
   */
  setPlaceHolderValues(){
      this.namePlaceHolder = this.translateService.instant('contact.name');
      this.companyPlaceHolder = this.translateService.instant('contact.company');
      this.emailPlaceHolder = this.translateService.instant('contact.email');
      this.mobilePlaceHolder = this.translateService.instant('contact.mobile');
      this.messagePlaceHolder = this.translateService.instant('contact.message');
  }

  /**
   * 
   */
  onSubmit(){
    // TO DO: Validation
  }

}
